/* Reset some basic elements to ensure the app takes up the full screen */
body, html, #root, .app {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

/* Base body styling */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Ensuring the app takes up full width and height */
.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

/* Styling the grid to take up remaining space and ensure it displays correctly */
.grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex: 1;
  width: 100%;
}

/* Styling each item in the grid */
.item {
  flex: 1 1 calc(25% - 20px); /* Adjusts to 4 items per row */
  margin: 10px;
  box-sizing: border-box;
  padding: 10px;
  cursor: pointer;
  text-align: center;
}

.item img {
  max-width: 100%;
  height: auto;
  display: block;
  margin: 0 auto;
}

.ad-container, .ad-container-bottom {
  flex: 1 1 100%;
  margin: 10px;
  padding: 20px;
  background-color: #f0f0f0;
  text-align: center;
}

/* Dark mode styling */
.dark {
  background-color: #333;
  color: #fff;
}

.light {
  background-color: #fff;
  color: #000;
}

/* Media queries for responsive design */
@media (max-width: 768px) {
  .item {
    flex: 1 1 calc(50% - 20px); /* Adjusts to 2 items per row on smaller screens */
  }
}

@media (max-width: 480px) {
  .item {
    flex: 1 1 calc(50% - 20px); /* Adjusts to 2 item per row on mobile screens */
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
