.app {
  text-align: center;
  padding: 20px;
}

.dark {
  background-color: #333;
  color: white;
}

.light {
  background-color: #f5fcff;
  color: black;
}

.grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.item {
  margin: 10px;
  padding: 10px;
  border-radius: 10px;
  width: 150px;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: border 0.3s;
}

.item-image {
  width: 100%;
  height: 100px;
  border-radius: 10px;
}

.ad-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 200px;
  width: 150px;
}

.ad-container-bottom {
  margin-top: 10px;
  height: 50px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.playing {
  border: 2px solid #00ff00;
}

.toggle-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}
